import { Component, Input, OnInit } from '@angular/core';
import { addDays, addHours, subDays } from 'date-fns';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOmniLovTypeRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsComplexCalendarExposed, IPsCalendarEvent } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from '../../../../commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexCallSlot } from './ps-complex-call-slot.component.interfaces';

const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3',
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF',
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA',
  },
};

@Component({
  selector: 'ps-complex-call-slot',
  templateUrl: './ps-complex-call-slot.component.html',
})

export class PsComplexCallSlotComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexCallSlot;
  dateTimeFormat;
  constructor(public commonProv: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService) {
    super(commonProv, loggerP);
  }
  cOptions: IOptionsPsComplexCalendarExposed = {
    events: [{
      start: addHours(subDays(new Date(), 1), 1),
      end: addHours(subDays(new Date(), 1), 2),
      title: 'Slot From ' + addHours(subDays(new Date(), 1), 1).toLocaleTimeString() + ' to ' + addHours(subDays(new Date(), 1), 2).toLocaleTimeString(),
      // actions: this.actions,
      color: colors.red,
      onClick: (returnedData): any => {
        this.bookSlot(returnedData);
      }
      // allDay: true,
      // resizable: {
      //   beforeStart: true,
      //   afterEnd: true,
      // },
      // draggable: true,
    },
    {
      start: addHours(new Date(), 1),
      end: addHours(new Date(), 2),
      title: 'Slot From ' + addHours(new Date(), 1).toLocaleTimeString() + ' to ' + addHours(new Date(), 2).toLocaleTimeString(),
      // actions: this.actions,
      color: colors.yellow,
      onClick: (returnedData): any => {
        this.bookSlot(returnedData);
      }
    },
    {
      start: addHours(addDays(new Date(), 1), 1),
      end: addHours(addDays(new Date(), 1), 2),
      title: 'Slot From ' + addHours(addDays(new Date(), 1), 1).toLocaleTimeString() + ' to ' + addHours(addDays(new Date(), 1), 2).toLocaleTimeString(),
      color: colors.blue,
      onClick: (returnedData): any => {
        this.bookSlot(returnedData);
      }
      // allDay: true,
    },
    {
      start: addHours(addDays(new Date(), 2), 1),
      end: addHours(addDays(new Date(), 2), 2),
      title: 'Slot From ' + addHours(addDays(new Date(), 2), 1).toLocaleTimeString() + ' to ' + addHours(addDays(new Date(), 2), 2).toLocaleTimeString(),
      // actions: this.actions,
      color: colors.yellow,
      onClick: (returnedData): any => {
        this.bookSlot(returnedData);
      }
      // resizable: {
      //   beforeStart: true,
      //   afterEnd: true,
      // },
      // draggable: true,
    }, {
      start: addHours(new Date(), 4),
      end: addHours(new Date(), 5),
      title: 'Slot From ' + addHours(new Date(), 4).toLocaleTimeString() + ' to ' + addHours(new Date(), 5).toLocaleTimeString(),
      // actions: this.actions,
      color: colors.yellow,
      onClick: (returnedData): any => {
        this.bookSlot(returnedData);
      }
      // resizable: {
      //   beforeStart: true,
      //   afterEnd: true,
      // },
      // draggable: true,
    },]
  }

  ngOnInit() {
    this.getDateTimeFormat();
    this.cOptions.group = this.options.group;
    this.cOptions.fcName = this.options.fcName;
  }

  async getDateTimeFormat() {
    const result = await this.omniPull.getParamValOf('DefaultDateTimeFormat');
    if (result) {
      const paramData: IOmniLovTypeRequest = {
        lovTypeId: CommonBussinessConstant.DEFAULT_DATE_TIME_LOV_TYPE_ID,
        lovCodesInlude: result['DefaultDateTimeFormat']
      };
      const res = await this.omniPull.returnLovTypesValues(paramData)
      if (res && res[0] && res[0].description) {
        this.dateTimeFormat = res[0].description
      }
    }
  }

  async bookSlot(data: IPsCalendarEvent) {
    return;
  }

}
